import React,{useState,useEffect} from 'react'
import './Travel.css'

function Travel({pagina}) {

   
    return (
        <div className="travel-desc">
         <div>
             <h4><b>Agents Commissions and Discount <br/>
                Program for Airport Transfers</b></h4>
             <br/>
            <p>Travel agent commissions and discount program for airport transfers.Earn commissions or discounted prices working with the Dominican transfer diamond program. Through our websites, travel agents can offer customers the easiest, cheapest and fastest transportation options. Our transfers are available to and from the main airports, ports, cities and tourist areas of the Dominican Republic.
            <br/>
            <br/>
            <br/>
            * With our automated reservation process: Travel agents can easily book airport transfers for their clients. Travel buyers can enjoy hassle-free travel to and from the airport, Commissions or discounted prices on all our airport transfers.
             <br/>
             <br/>
             * Discounts for travel agencies: If you prefer to get discounted prices, simply complete the registration form below and after approval you will receive a discount code (Token) valid for 15% that will be used during the reservation process.
             <br/>
             <br/>
             * Commissions for Travel Agencies: If you prefer to get a 15% commission, you must also fill out the registration form below, wait to be approved and you also need the following:Open a PayPal account at www.paypal.com and email us for that account to send your commission through PayPal. Would you rather receive a commission for ACH? (USA only) If you are located within the US We can send your commission through an ACH deposit, follow the instructions.
             <br/>
             <br/>
             <br/>
             Send us your business name, address, bank name, routing number, account number, account type, and email address. Commissions will be paid when the Agency accumulates a minimum of US $ 300.00 dollars. Which means that on more bookings, the commission will be paid faster and you will earn more money out of pocket.
             <br/>
             <br/>
             All agencies will be able to see in real time all the reservations made and the commissions accumulated so far. DIAMOND will provide you with the means to keep track of all your reservations and commissions.
             <br/>
             <br/>
             SIGN UP and you will receive a unique travel agent CODE. Use this code to get discounts or commissions only on transfers. Just insert the code in the field marked as Travel Agent and you will automatically get a discounted price or your commissions will be published in your reservations and commission report.
             <br/>
             <br/>
             If the Travel Agent does not expect to reserve transfers at least once a month on a continuous basis, we suggest that you do not register. The system keeps a count of the number of reservations per account and, if the agency does not have reservations in two consecutive months, it will automatically deactivate without prior notice.
            </p> <br/> <br/>
         </div>
        
        </div>
        
    
    )

}

export default Travel;