import React,{useState,useEffect} from 'react'
import ContactoInformacion from './contactanos/ContactoInformacion'
import ContactoInformacionPagina from './contactanos/ContactoInformacionPagina'
import FormularioContacto from './contactanos/FormularioContacto'


function    Contactanos({pagina}) {
    const [Clases, setClases] = useState({
        contactoContainer:"contacto_container",
        contactoInformacion:"contacto_informacion",
        contactoFormulario:"contacto_formulario"
    })

    useEffect(()=>{
        if(pagina){
            setClases({
                contactoContainer:"contacto_container_pagina",
                contactoInformacion:"contacto_informacion_pagina",
                contactoFormulario:"contacto_formulario_pagina"
            })
        }
        
    },[])

    

    
    return (
        <div className="contacto">
            <div className="contenedor">

            {/*{ pagina? null:<p className="contacto_titulo">Contactános</p>}*/}

            <div className={Clases.contactoContainer}>
            <div className={Clases.contactoFormulario}><FormularioContacto/></div>
            </div>
                <div className={Clases.contactoInformacion}>
                   {pagina?<ContactoInformacionPagina/>:<ContactoInformacion/>} 
                    
                    </div>
            
            </div>
           
            
        </div>
    )
}

export default Contactanos
