import React, { useState } from 'react'
import { Form, Col, Button, Row } from 'react-bootstrap';

function FormularioContacto() {
    
    const [datos, setdatos] = useState({
        nombre:"",
        correo:"",
        telefono:"",
        mensaje:"",
       
    })

    const actualizar=(event)=>{
          
        setdatos({
          ...datos,
          [event.target.name]:event.target.value
        })

        console.log(datos)
        

    }




   const enviar_correo=async(event)=>{
    event.preventDefault()
    let form=  new FormData()
    form.append("nombre",datos.nombre)
    form.append("correo",datos.correo)
    form.append("telefono",datos.telefono)
    form.append("mensaje",datos.mensaje)
   
    
    try{
        const resultado =  await fetch("",{
            method:"post",
            body:form
        }) 
            alert("correo enviado correctamente")
            document.querySelector("#formulario_contacto").reset()


        }catch(e){
            alert("no se pudo enviar el correo ")
        }



    
    
    
    }

    return (
        
        <Form className="fomulario_contacto" id="formulario_contacto" onSubmit={enviar_correo}>
            <Form.Group controlId="formBasicEmail" as={Row} >
                <Col xs ={4}>
                    <Form.Control className="fomulario-contacto-nombre" placeholder="Ingresa tu nombre" name="nombre" onChange={actualizar}   required/>
                </Col>
                <Col >
                    <Form.Control className="fomulario-contacto-email" placeholder="Escriba el correo Electronico" type="email" name="correo" onChange={actualizar}  required/>
                </Col>
                <Col>
                    <Form.Control className="fomulario-contacto-number" placeholder="Ingresa tu telefono" type="number" name="telefono" onChange={actualizar}  required/>
                </Col>
            </Form.Group>
            <Form.Group as={Row}>
                
            </Form.Group>
            <Form.Group as={Row}>
               
            </Form.Group>
            <Col>
            <Form.Group as={Row}>
                <Form.Control as="textarea" rows={3} name="mensaje" placeholder="Escriba su mensaje" onChange={actualizar}  ></Form.Control>

            </Form.Group>
            </Col>

            <div className="fomulario-contacto-enviar">
            <Button variant="primary"  type="submit">
                Enviar
                </Button>
            </div>
        </Form>
    )
}

export default FormularioContacto
