import React, { useState ,useMediaQuery} from 'react'
import { Fragment } from 'react'
import { Link,Redirect } from 'react-router-dom'


function Header() {
    const ipad= window.matchMedia('screen and (max-width:758px)'); 
    const [activo, setactivo] = useState(false)
    
    
    const toggogle_menu= ()=>{
        setactivo(!activo)
    }

    const cerrar_menu=()=>{
        if(ipad.matches){
            setactivo(false)
        }
        
    }

    return (

        
        <header className="header ">
                
            <i class={`icon-menu ${activo?" burger-button is-active": "burger-button"}`}  id="boton-menu" onClick={toggogle_menu}></i>
            <div className="contenedor">

                <Link onClick={cerrar_menu} className="logo" to="/" >
                <a>
                                <img className="bandera" src="https://dominicantransfersdiamond.com/wp-content/uploads/2019/01/diamond2.png" width="167px" alt="bandera" />
                                </a>
                 </Link>

    
                <nav className={activo?"menu is-active": "menu"}>
                  
                    <ol >   
                             <li >
                            
                            <Link onClick={cerrar_menu} className="link" to="/home">Home</Link>
                            </li>

                            <li >
                            
                            <Link onClick={cerrar_menu} className="link" to="/covid">COVID19 UPDATA</Link>
                            </li>
             
                             <li >
                            
                                <Link onClick={cerrar_menu} className="link" to="/travel">TRAVEL AGENTS</Link>
                            </li>
                 
                            <li >
                            
                                <Link onClick={cerrar_menu} className="link" to="/company">OUR COMPANY</Link>
                            </li>
                 
                           

                            <li>
                                <Link onClick={cerrar_menu} className="link" to="/tours">TOURS</Link>
                            </li>
                            <li>
                                <Link className="link" onClick={cerrar_menu} to="/gallery">GALLERY</Link>
                            </li>

                                
                            <li>
                                <Link onClick={cerrar_menu} type="button" className=" btn btn-primary" to="/contacto" >Contacto</Link>
                            </li>

                            {/*<li >
                            
                            <Link onClick={cerrar_menu} className="link" to="/lenguaje">LENGUAJE</Link>
                            </li>*/}
                           
                    </ol>
         
                </nav>
        
        </div>
    
    
    </header>
    
    )
}

export default Header
