import React, { useState ,useMediaQuery} from 'react'
import { Fragment } from 'react'
import { Link,Redirect } from 'react-router-dom'
import Twitter from '../img/Twitter.png'
import Instagram from '../img/Instagram.png'
import Facebook from '../img/Facebook.png'
import puerto from '../img/puerto.png'





function Nav() {
    const ipad= window.matchMedia('screen and (max-width:758px)'); 
    const [activo, setactivo] = useState(false)
    
    
    const toggogle_menu= ()=>{
        setactivo(!activo)
    }

    const cerrar_menu=()=>{
        if(ipad.matches){
            setactivo(false)
        }
        
    }
    
    return (

        
        <nav className="nav ">
                
            <i class={`icon-menu ${activo?" burger-button is-active": "burger-button"}`}  id="boton-menu" onClick={toggogle_menu}></i>
            <div className="contenedor">

                <Link onClick={cerrar_menu} className="logo" to="/" >
                 
                 </Link>


                <nav className={activo?"menu is-active": "menu"}>
                    <ol >
                         
                           <div className="correo">
                            <li>
                              
                               <h6>info@dominicantransfersdiamond.com</h6>
                              
                              
                            </li>
                            </div>
                            <div className="correo">
                            <li>
                              
                               <h6>Tel : +57 319 220 0959</h6>
                              
                              
                            </li>
                            </div>
                            <div className="nav-img">
                            <li>
                                <img src={puerto} alt="puerto"></img>
                                
                            </li>
                            </div>
                            <div>
                            <li>
                               
                                <a href="https://twitter.com/DiamondTranspo3">
                                <img src={Twitter} alt="twitter"></img>
                                </a>
                            </li>
                            </div>
                            <div>
                            <li>
                               
                                <a href="https://www.instagram.com/dominicantransfersdiamond/">
                                <img src={Instagram} alt="instagram"></img>
                                </a>
                            </li>
                            </div>
                            <div>
                            <li>
                                
                                <a href="https://web.facebook.com/dominicantransfersdiamond">
                                <img src={Facebook} alt="facebook"></img>
                                </a>
                            </li>
                            </div>
                            

                
            </ol>
        </nav>
        
         
    </div>
    
    </nav>
    
    )
}

export default Nav
