import React from 'react'
import Contactanos from '../Home/Contactanos'
import ContactoTitulo from '../Home/contactanos/ContactoTitulo'
import ContactoImg from '../Home/contactanos/ContactoImg'

function Contacto() {
    return (
        <div className="Contacto_pagina">
                <ContactoTitulo />
                <Contactanos pagina={true}/>
                <ContactoImg />
        </div>
    )
}

export default Contacto
